:root {
  --blue: hsl(246, 80%, 60%);
  --darkBlue: hsl(235, 46%, 20%);
  --veryDarkBlue: hsl(226, 45%, 10%);
  --lightBlue: hsl(235, 45%, 61%);
  --paleBlue: hsl(236, 100%, 87%);
  --hoverBlue: hsl(236, 41%, 34%);
  --white: hsl(0, 100%, 100%);
  --work: hsl(15, 100%, 70%);
  --play: hsl(195, 74%, 62%);
  --study: hsl(348, 100%, 68%);
  --exercise: hsl(145, 58%, 55%);
  --social: hsl(264, 64%, 52%);
  --selfCare: hsl(43, 84%, 65%);
}

:root {
  --font-rubik: "Rubik", sans-serif;
}

.report {
  display: grid;
  row-gap: 2.4rem;
  /* Work */
  /* Play */
  /* Study */
  /* Exercise */
  /* Social */
  /* Self Care */
}
@media (min-width: 62em) {
  .report {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(4, 20rem);
    grid-template-areas: "person work play study" "person exercise social selfCare";
    column-gap: 2.4rem;
  }
}
@media (min-width: 75em) {
  .report {
    grid-template-columns: repeat(4, 25.5rem);
    column-gap: 3rem;
    row-gap: 3rem;
  }
}
.report__content {
  border-radius: 15px;
}
.report__content:first-child {
  background-color: var(--darkBlue);
}
.report__content:not(:first-child) {
  padding-top: 4rem;
  cursor: pointer;
}
@media (min-width: 62em) {
  .report__content:not(:first-child) {
    display: grid;
    padding-top: 4.5rem;
  }
}
@media (min-width: 62em) {
  .report__person {
    grid-area: person;
  }
}
.report__person-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
  padding: 3.5rem;
  background-color: var(--blue);
  border-radius: 15px;
}
@media (min-width: 62em) {
  .report__person-inner {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 3.2rem;
    padding-bottom: 8rem;
  }
}
.report__person-img {
  width: 6.4rem;
  height: 6.4rem;
  border-radius: 50%;
  border: 3px solid var(--white);
}
@media (min-width: 62em) {
  .report__person-img {
    margin-bottom: 4.3rem;
    width: 7.8rem;
    height: 7.8rem;
  }
}
.report__status-for {
  font-size: 1.5rem;
  color: var(--paleBlue);
  margin-bottom: 0.4rem;
}
.report__period {
  padding: 2.4rem;
  display: flex;
  justify-content: space-between;
}
.report__period-text {
  color: var(--lightBlue);
  cursor: pointer;
  transition: 0.4s;
}
.report__period-text:hover {
  color: var(--white);
}
.report__period-text.active {
  color: var(--white);
}
@media (min-width: 62em) {
  .report__period {
    padding: 2.6rem 3.2rem;
    flex-direction: column;
    row-gap: 1.2rem;
  }
}
.report__inner {
  width: 100%;
  padding: 2.8rem 2.4rem;
  background-color: var(--darkBlue);
  border-radius: 15px;
  transition: background-color 0.4s;
}
.report__inner:hover {
  background-color: var(--hoverBlue);
}
@media (min-width: 62em) {
  .report__inner {
    padding: 3rem;
  }
}
.report__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.6rem;
}
.report__ellipsis {
  cursor: pointer;
}
.report__data {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.report__data-previous {
  font-size: 1.5rem;
  font-weight: 300;
  color: var(--paleBlue);
}
@media (min-width: 62em) {
  .report__data {
    flex-direction: column;
    align-items: flex-start;
  }
}
.report__work {
  background-color: var(--work);
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.report__work::after {
  content: url("../images/icon-work.svg");
  position: absolute;
  top: -1rem;
  right: 1.8rem;
  z-index: -1;
}
@media (min-width: 62em) {
  .report__work {
    grid-area: work;
  }
}
.report__play {
  background-color: var(--play);
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.report__play::after {
  content: url("../images/icon-play.svg");
  position: absolute;
  top: -1rem;
  right: 1.8rem;
  z-index: -1;
}
@media (min-width: 62em) {
  .report__play {
    grid-area: play;
  }
}
.report__study {
  background-color: var(--study);
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.report__study::after {
  content: url("../images/icon-study.svg");
  position: absolute;
  top: -1rem;
  right: 1.8rem;
  z-index: -1;
}
@media (min-width: 62em) {
  .report__study {
    grid-area: study;
  }
}
.report__exercise {
  background-color: var(--exercise);
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.report__exercise::after {
  content: url("../images/icon-exercise.svg");
  position: absolute;
  top: -1rem;
  right: 1.8rem;
  z-index: -1;
  top: -0.1rem;
}
@media (min-width: 62em) {
  .report__exercise {
    grid-area: exercise;
  }
}
.report__social {
  background-color: var(--social);
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.report__social::after {
  content: url("../images/icon-social.svg");
  position: absolute;
  top: -1rem;
  right: 1.8rem;
  z-index: -1;
}
@media (min-width: 62em) {
  .report__social {
    grid-area: social;
  }
}
.report__care {
  background-color: var(--selfCare);
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.report__care::after {
  content: url("../images/icon-self-care.svg");
  position: absolute;
  top: -1rem;
  right: 1.8rem;
  z-index: -1;
}
@media (min-width: 62em) {
  .report__care {
    grid-area: selfCare;
  }
}

.main {
  background-color: var(--veryDarkBlue);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper {
  max-width: 32.7rem;
  margin: 0 auto;
  padding-top: 8rem;
  padding-bottom: 8rem;
}
@media (min-width: 62em) {
  .wrapper {
    max-width: 96rem;
  }
}
@media (min-width: 75em) {
  .wrapper {
    max-width: 114rem;
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  font-family: var(--font-rubik);
  color: var(--white);
}

.heading-primary {
  font-size: 2.4rem;
  font-weight: 300;
  line-height: 1;
}
@media (min-width: 62em) {
  .heading-primary {
    font-size: 3.2rem;
    line-height: 1.2;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    font-size: 4rem;
    line-height: 1.2;
  }
}

.heading-secondary {
  font-size: 1.8rem;
  font-weight: 500;
}

.heading-tertiary {
  font-size: 3.2rem;
  font-weight: 300;
}
@media (min-width: 62em) {
  .heading-tertiary {
    font-size: 4.8rem;
  }
}
@media (min-width: 75em) {
  .heading-tertiary {
    font-size: 5.6rem;
  }
}